.pets {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  padding: 40px;
  padding-right: 0;
}
.pets #alert-dialog-description p {
  line-height: 2 !important;
}
.pets .title {
  text-align: center;
  font-size: 60px;
  margin-bottom: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pets .title .pets-icon {
  display: flex;
  align-items: center;
  margin-top: 24px;
  width: 25%;
}
.pets .title .remaining-count {
  font-size: 8.5rem;
  font-weight: 600;
  display: flex;
  z-index: 1;
}
.pets .title .remaining-count sup {
  font-size: 1rem;
  text-transform: uppercase;
}
.pets .desc {
  text-align: left;
  font-size: 20px;
  line-height: 1.8;
}
.pets .card {
  width: 80%;
  margin: 0 auto;
}
.pets .mint-card {
  padding: 20px;
  background: #000;
}
.pets .mint-card .token-input * {
  color: #fff !important;
}
.pets .mint-card .button {
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  margin: 24px 0;
}
.pets .mint-card .button .mint-button {
  text-transform: unset;
  color: #030303;
}
.pets .rules-button {
  text-transform: unset;
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}
@media (max-width: 480px) {
  .pets {
    padding: 0;
  }
}
