.role-plus {
  min-height: 100vh;
  margin-top: 40px;
}
.role-plus .role-pic {
  display: flex;
  justify-content: center;
  position: relative;
  width: 100%;
}
.role-plus .role-pic .role-with-loot {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  padding: 24px;
}
.role-plus .role-pic .role-with-loot .role-svg {
  width: 60%;
  margin: 0 auto;
  bottom: 0;
}
.role-plus .role-pic .role-with-loot .left-loot {
  position: relative;
  align-self: flex-start;
}
.role-plus .role-pic .role-with-loot .loot-item {
  background: chocolate;
  margin-top: 20px;
  padding: 12px;
}
.role-plus .role-pic .role-with-loot .right-loot {
  position: relative;
  align-self: flex-start;
}
.role-plus .role-pic .pet-svg {
  position: absolute;
  width: 30%;
  bottom: 0;
  right: 0;
}
.role-plus .card-list {
  margin-top: 40px;
  background: #23222b;
  padding: 20px;
  width: 90%;
  margin: 0 auto;
  max-height: 60vh;
  overflow-y: scroll;
}
