.App {
  text-align: center;
  background: #000;
  min-height: 100vh;
  font-family: "Georgia";
  color: #fff;
}
.App #confetti {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  z-index: 99;
}
.App .logo {
  padding: 40px;
  padding-bottom: 0;
  font-family: "Roboto";
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.App .logo .left {
  display: flex;
  justify-content: space-between;
  width: 30%;
  max-width: 324px;
}
.App .logo .left img {
  cursor: pointer;
  width: 40%;
  filter: brightness(0.4);
}
.App .logo .left .active-route {
  filter: brightness(1);
}
.App .logo .right {
  display: flex;
  justify-content: flex-end;
}
.App .logo .right .media {
  width: 8%;
  margin-left: 60px;
  max-width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.App .logo .right .media a {
  color: #fff;
}
.App .logo .right .media img {
  margin-bottom: 10px;
}
.App .logo .title {
  margin-top: 10px;
}
.App .footer {
  background: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.App .footer .footer-logo {
  width: 10%;
  max-width: 100px;
}
.App .footer .infos {
  margin-top: 20px;
}
@media (max-width: 420px) {
  .App .logo .left {
    width: 80%;
    max-width: 400px;
  }
}
